.project-info-container {
  margin: auto;
  max-width: 800px;
  padding: 0 5%;
  margin-bottom: 50px;
}

.project-title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2em;
}

.project-language {
  font-size: 0.8em;
  color: rgb(89, 89, 89);
  font-weight: 500;
  margin: 20px 0;
  font-weight: 700;
}

.language-divider {
  color: var(--primary-color);
  margin: 0 .25em;
}

.project-link {
  color: var(--primary-color);
}

.project-link:hover {
  color: var(--primary-color-hover);
}

.project-not-found {
  display: flex;
  flex-direction: column;
  height: 50vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  gap: 15px;
}

.project-not-found {
  text-align: center;
}

/*DESKTOP*/
@media screen and (min-width: 992px) {
  .project-info-container{
    padding: 0;
  }
  .project-title {
    font-size: 3em;
  }

  .project-not-found {
    font-size: 2em;
  }
}